import {getCategory} from '@/api/homeApi';
export default {
    name: "Category",
    data() {
        return {
            category: []
        }
    },
    mounted() {
        uni.setNavigationBarTitle({title: '分类'});
        this.getCategoryData();
    },
    methods: {
        async getCategoryData() {
            let res = await getCategory();
            this.category = res.data.res.category;
        } 
    }
}
